import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Comment, Segment } from 'semantic-ui-react';

class Message extends Component{
  render(){
      const {message} = this.props;
      var commentContext = <Comment.Content size='large'>
                             <Comment.Author as='a'>{message.author}</Comment.Author>
                             <Comment.Metadata>
                               <div>{message.createdAt}</div>
                             </Comment.Metadata>
                             <br/>
                             <Segment style={messageSegmentStyle}>
                               <Comment.Text style={textStyle}>{message.message}</Comment.Text>
                             </Segment>
                           </Comment.Content>;

      if(this.props.isMessageFromCurrentUser(message.author) == true){
        return(
          <Comment style={commentAlignment}>
            {commentContext}
          </Comment>
        )
      }else {
        return(
            <Comment>
              {commentContext}
            </Comment>
        )
      }
  }
}

Message.propTypes = {
  message: PropTypes.object.isRequired,
  isMessageFromCurrentUser: PropTypes.func.isRequired,
}
var commentAlignment = {
  textAlign: 'right',
  marginBottom: '20px'
};

var messageSegmentStyle = {
    display: 'inline-block',
    backgroundColor: 'dodgerblue',
    borderRadius: '1rem',
    maxWidth: '500px'
}

var textStyle = {
  fontSize: '15px',
  color: 'white',
  textAlign: 'initial'
}
export default Message



