import React, {Component} from 'react';
import {Container, Grid, Image} from 'semantic-ui-react';
import AuthenticationSection from './authentication/AuthenticationSection.jsx';
import AppSection from './AppSection.jsx';
import Socket from '../socket.js';
class App extends Component{
  constructor(props){
    super(props);
    this.state = {
      channels: [],
      users: [],
      errors: '',
      connected: false,
      activeChannel: {
        name: ''
      },
      showAddUserForm: true,
      authenticationError : [],
      signupError : [],
      user : {},
      authenticated : false,
      messages: [],
    };
  }

  authenticate(email, password){
    var authenticationError = [];
    let {user} = this.state;
    if(user.email != email){
      authenticationError.push("Invalid email")
    }
    if(user.password != password){
      authenticationError.push("Incorrect password")
    }
    let {authenticated} = this.state;
    authenticated = true;
    this.setState({authenticated});
    this.setState({authenticationError});
  }

  signup(firstName, lastName, email, password, avatarUrl ,resetToSignIn){
    let user = this.state.user;
    user.firstName = firstName;
    user.lastName = lastName;
    user.email = email;
    user.password = password;
    user.avatarUrl = avatarUrl;
    this.setState({user})
    var signupError = [];
    if(signupError.length == 0){
      resetToSignIn();
    }
  }

  resetErrorsOnSignin(){
    var authenticationError = [];
    this.setState({authenticationError});
  }

  isMessageFromCurrentUser(author){
      if(author == this.state.activeUser){
          return true;
      }
      return false;
  }

  addChannel(name){
    this.socket.emit('channel add', {name});
  }
  setChannel(activeChannel){
    this.setState({activeChannel});
    this.socket.emit('message unsubscribe');
    this.setState({messages: []});
    this.socket.emit('message subscribe', {
      channelId: activeChannel.id
    });
  }
  addUser(name){
    if(this.state.activeUser != null){
      return
    }
    let activeUser = name
    this.setState({activeUser});
    this.socket.emit('user add', {name});
    this.setState({
      showAddUserForm: false
    })
  }
  setUser(activeUser){
    // console.log(activeUser);
  }

  addMessage(message){
    console.log(message);
    var errors = [];
    if (this.state.activeUser == null) {
      errors.push("Please select a user");
    }
    if (this.state.activeChannel.name == '') {
      errors.push("Please select a Channel");
    }
    if(errors.length == 0){
      this.socket.emit('message add', {message});
    }
    this.setState({errors});
  }

  componentDidMount(){
    let ws = new WebSocket('ws://localhost:4000')
    let socket = this.socket = new Socket(ws);
    socket.on('connect', this.onConnect.bind(this));
    socket.on('disconnect', this.onDisconnect.bind(this));
    socket.on('channel add', this.onAddChannel.bind(this));
    socket.on('user add', this.onAddUser.bind(this));
    socket.on('user edit', this.onEditUser.bind(this));
    socket.on('user remove', this.onRemoveUser.bind(this));
    socket.on('message add', this.onMessageAdd.bind(this));
  }

  onAddChannel(channel){
    let {channels} = this.state;
    channels.push(channel);
    this.setState({channels});
  }

  onMessageAdd(message){
      console.log(message);
      let {messages} = this.state;
      messages.push(message);
      this.setState({messages})
  }

  onRemoveUser(removeUser){
    // console.log(removeUser);
    let {users} = this.state;
    // console.log(users);
    users = users.filter( user => {
      return removeUser.id != user.id;
    });
    // console.log(users);
    this.setState({users});
    // console.log(this.state.users);
  }

  onAddUser(user){
    let {users} = this.state;
    users.push(user);
    this.setState({users});
  }

  onEditUser(editUser){
    let {users} = this.state;
    users.map( user => {
      if(editUser.id == user.id){
        return editUser;
      }else{
        return user;
      }
    });
    this.setState({users});
  }

  onConnect(){
    this.setState({connected: true});
    this.socket.emit('channel subscribe');
    this.socket.emit('user subscribe');
  }

  onDisconnect(){
    this.setState({connected: false});
  }

  render(){
    var app = null;
    // if ( this.state.authenticated ){
    if ( true ){
      app = <AppSection
                channels={this.state.channels}
                addChannel={this.addChannel.bind(this)}
                setChannel={this.setChannel.bind(this)}
                addMessage={this.addMessage.bind(this)}
                messages={this.state.messages}
                isMessageFromCurrentUser={this.isMessageFromCurrentUser.bind(this)}
                users={this.state.users}
                addUser={this.addUser.bind(this)}
                setUser={this.setUser.bind(this)}
                showAddUserForm={this.state.showAddUserForm}
              />;
    } else {
      app = <AuthenticationSection
        authenticate={this.authenticate.bind(this)}
        authenticationError={this.state.authenticationError}
        resetErrorsOnSignin={this.resetErrorsOnSignin.bind(this)}
        signup={this.signup.bind(this)}
      />;
    }
    return(
        <Container fluid style={{minWidth: '100%'}}>
          {app}
        </Container>
    )
  }
}

export default App;



